@import url("https://fonts.googleapis.com/css?family=Montserrat&display=swap");
* {
  font-family: "Monserrat", sans-serif; }

#topbar {
  width: 100%;
  border-bottom: 1px solid black;
  background: linear-gradient(black, #2A2A8E); }
  #topbar #navbar {
    width: 100%;
    background: linear-gradient(black, #2A2A8E); }
  #topbar img {
    margin: 10px;
    max-width: 100%;
    height: auto;
    height: 50px; }
  #topbar ul {
    padding: 0;
    text-align: center;
    margin-top: -30px;
    display: block; }
  #topbar li {
    font-size: 0.8em;
    margin-top: 30px;
    padding: 10px;
    display: inline-block; }
    #topbar li a {
      color: white;
      text-decoration: none; }
      #topbar li a:hover {
        color: gray; }

@media (min-width: 768px) {
  #topbar ul {
    text-align: left;
    margin-top: 0; }
  #topbar li {
    font-size: 1.3em;
    margin-left: 10px;
    margin-right: 10px; }
  #topbar img {
    height: 85px; } }
